import React from 'react';
import PropTypes from 'prop-types';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import styled from 'styled-components';

const Dot = styled.div`
  background: ${props => props.theme.red};
  color: white;
  border-radius: 50%;
  padding: 0.5rem;
  line-height: 2rem;
  min-width: 3rem;
  margin-left: 1rem;
  font-weight: 100;
  font-feature-settings: 'tnum';
  font-variant-numeric: tabular-nums;
`;
//font font-feature-settings and font-variant-numeric give the same width to the number no matter the size of the number ie: '1' and '2 is printed on the screen as the same width

const CartCount = ({ count }) => (
  <TransitionGroup>
    <CSSTransition 
      classNames="count" 
      classNames="count" 
      key={count} 
      timeout={{enter: 4000, exit: 4000}}
    >
      <Dot>{count}</Dot>
    </CSSTransition>
  </TransitionGroup>
);

export default CartCount;